.btn {
    padding: 10px 20px;
    border-radius: 3px;
    outline: none;
    font-size: 1rem;
}

.btn-primary {
    border: 2px solid #f5f5f5;
    background-color: transparent;
    color: #f5f5f5;
}

/** OTHER STYLES **/
.btn-subscribe {
    padding: 10px 15px;
    position: relative;
    cursor: pointer;
}

.btn-subscribe:active {
    transform: translateY(3px);
}

.btn-subscribe::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: scaleX(0);
    transform-origin: right;
    background-color: currentColor;
    opacity: 0.5;
    transition: transform 200ms ease-in;
}

.btn-subscribe:hover::after {
    transform: scaleX(1);
    transform-origin: left;
}

