.cursor {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #050505;
    position: absolute;
    transition-duration: 200ms;
    transition-timing-function: ease-out;
    -webkit-animation: cursorAnimate .5 infinite alternate;
            animation: cursorAnimate .5 infinite alternate;
}

@-webkit-keyframes cursorAnimate {
    from {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to {
        -webkit-transform: scale(.7);
                transform: scale(.7);
    }
}

@keyframes cursorAnimate {
    from {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to {
        -webkit-transform: scale(.7);
                transform: scale(.7);
    }
}
